import router from './router'
import {aysncRouterMap,constantRouterMap} from './router'
import store from './store'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css'// Progress 
import { Message } from 'element-ui'
import { getToken } from '@/utils/auth' // 

var getRouter=false;

const whiteList = ['/login'] // 
router.beforeEach((to, from, next) => {
  NProgress.start();

if (localStorage.hasOwnProperty('WMuserInfo')) {//getToken
   next();
      if(!getRouter){
          if (store.getters.roles===2||store.getters.roles===1||store.getters.roles===3) {
           store.dispatch('GenerateRoutes',constantRouterMap.concat(aysncRouterMap)).then(res=>{//
            getRouter=true;
            next({ ...to, replace: true })
           })

            // store.dispatch('GenerateRoutes').then(res => {// 
            //   router.addRoutes(aysncRouterMap)
            //  next({ ...to, replace: true }) // hack addRoutes
            // }).catch((err) => {
            //   store.dispatch('FedLogOut').then(() => {
            //     console.log('err')
            //     Message.error(err || 'Verification failed, please login again')
            //     next({ path: '/' })
            //   })
            // })
          } else {//
            store.dispatch('GenerateRoutes',aysncRouterMap).then(res=>{
              console.log(res);
            })
            next()
          }


      }

  }else {
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next('/login')
      NProgress.done()
    }
  }
})

router.afterEach(() => {
  NProgress.done() // Progress
})





